    list-style: none;
}



.exp1 ul.menu-drop1, .exp2 ul.menu-drop2, .exp3 ul.menu-drop3 {
    position: absolute;
    display: block;
    font-family: var(--font-family);
    font-weight: 550;
    font-size: 22px;
    line-height: 40px;
     color: #A32D50;
}

