    padding-top: 5rem;
    padding-bottom: 5rem;
}

.menu-drop1 li::before, .menu-drop2 li::before, .menu-drop3 li::before {
    content: "\2BC8";
    position: relative;
    color: var(--color-arrow);
    display: inline-block;
    width: 2rem;
}

