.project-container__article {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: var(--color-title);
}

.project-container__article-image h3 {
    width: 100%;
    height: 100%;
    border: 10px;
    background: black;
}

.project-container__article-image img {
    width: 100%;
    height: 100%;
}

.project-container__article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 100%;
}

.project-container__article-content p {
    font-family: var(--font-family);
    font-size: 20px;
    font-weight: bold;
    line-height: 35px;
    color: white;
}


.project-container__article-content h3 {

    font-family: var(--font-family);
    font-weight: 800;
    font-size: 25px;
    line-height: 30px;
    color: black;
    margin-bottom: 5rem;
}

.project-container__article-content p1 {
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 18px;
    line-height: 35px;
    color: #FF8C61;
}

.project-container__article-content a {
    font-family: var(--font-family);
    font-size: 20px;
    font-weight: bold;
    line-height: 35px;
    color: #2B4162;
}


.project-container__article-content p:last-child /*cursor allow it to click */ {
    cursor: pointer;
}

@media screen and (max-width: 550px) {

    .project-container__article-content h3 {
        font-size: 18px;
        line-height: 25px;
    }
    
}
.project {
  display: flex;
  flex-direction: column;
}

.project-heading {
  width: 100%;
  text-align: left;
  margin-bottom: 5rem;
}

.project-heading h1 {
  margin-right: 1rem;
  font-family: var(--font-family);
  color: var(--color-title) ;
  font-weight: 800;
  font-size: 96px;
  line-height: 116px;
}

.project-container {
  display: flex;
  flex-direction: row;

}

.project-container__groupA {
  flex: 0.75 1;
  margin-right: 2rem;
}

.project-container__groupB {
  flex: 1 1;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
}

@media screen and (max-width: 990px) {
  .project-container {
      flex-direction: column-reverse;
  }

  .project-container__groupA {
      margin: 2rem 0;
  }

  .project-container__groupA .project-container__article {
      width: 48%;
  }
  .project-container__groupA .project-container__article-image {
      height: 250px;
  }
}

@media screen and (max-width: 700px) {
  .project-container__groupB {
      grid-template-columns: repeat(1, 1fr);

  }

  .project-container__groupA .project-container__article {
      width: 100%;
  }

  .project-heading h1 {
      font-size: 46px;
      line-height: 52px;
  }
}

@media screen and (max-width: 550px) {
  .project-heading h1 {
      padding-top: 2rem;
      font-size: 50px;
      line-height: 42px;
  }
}



